import { NextRequest, NextResponse } from 'next/server';

export async function POST(req: NextRequest) {
  try {
    const { datos } = await req.json();

    // Llamada al API externo
    const res = await fetch('https://votacion-api-7592.onrender.com/predecir', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ datos }),
    });

    if (!res.ok) {
      return NextResponse.json({ error: 'Error al obtener predicci√≥n del servidor externo' }, { status: res.status });
    }

    const data = await res.json();
    return NextResponse.json({ prediccion: data.prediccion });
  } catch (err) {
    return NextResponse.json({ error: 'Error interno en el servidor' }, { status: 500 });
  }
}
